.app {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row-reverse;
      flex-direction: row-reverse;
  -ms-flex-pack: left;
      justify-content: left;
  -ms-flex-align: center;
      align-items: center;
  height: 100vh;
  width: 100vw;
  background: url("../images/bg.jpeg") no-repeat;
  background-position: left center;
  background-size: auto;
}

.light {
  display: none;
}

.dark {
  display: block;
}

.form-card {
  width: 500px;
  height: 80vh;
  background: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  padding: 10vh 0;
}

.logo {
  padding: 0 50px;
  height: 25vh;
}

.select-wrapper {
  width: calc(100% - 100px);
  padding: 1vh 50px;
  font-variant: small-caps;
  font-family: "Montserrat";
  text-transform: lowercase;
  font-weight: 600;
  max-width: 450px;
}

.download-btn {
  margin-top: 2vh !important;
  width: 100% !important;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  font-variant: small-caps;
  font-family: "Montserrat";
  text-transform: lowercase;
  font-weight: 600;
  height: 40px !important;
}

.upload-text {
  font-variant: small-caps !important;
  font-family: "Montserrat";
  text-transform: lowercase;
  font-weight: 600 !important;
}

@media only screen and (max-width: 1000px) {
  .form-card {
    width: 80vw;
    padding: 10vh 10vw;
    background: #21212b77;
  }

  .dark {
    display: none;
  }

  .light {
    display: block;
  }
}

@media only screen and (max-width: 600px) {
  .form-card {
    width: 100vw;
    padding: 10vh 0;
  }

  .altLogo {
    display: none;
  }
}

.altLogo {
  position: fixed;
  width: 80px;
  top: 5px;
  left: 10px;
}

.back-btn {
  position: fixed;
  top: 25px;
  right: 25px;
  font-variant: small-caps;
  font-family: "Montserrat";
  text-transform: lowercase;
  font-weight: 600;
}



